import notice from '@/components/notice/index.vue'
import uniPopup from '@/components/uni-popup/uni-popup.vue'
import downTime from '@/components/downTime/index.vue'
import { mapState, mapMutations, mapGetters } from 'vuex'
export default {
	data() {
		return {
			newPersonGift: false,
			curStartTime: '',
			day: '0',
			hour: '00',
			min: '00',
			second: '00',
			newPersonData: '',
			newPersonList: '',
			hotList: '',
			bargainData: ''
		}
	},
	computed: {
		...mapGetters(['getLoginStatus','token'])
	},
	components: {
		notice,
		uniPopup,
		downTime
	},
	onLoad() {
		this.$http.get('/bargain_v1/active/default',{params: {token: this.token}})
			.then(res => {
				console.log(res.data);
				let data = res.data.data;
				if(res.data.code == 1){
					this.newPersonData = data;
				}
			}).catch(err => {
				console.log(err);
			})
			
	},
	onShow() {
		this.unitData();
	},
	methods: {
		...mapMutations(['login','setToken']),
		unitData(){
			if(this.token){
				this.getUserStatus();
			}
			this.getHotList();
			if(this.newPersonGift){
				this.getNewPersonList();
			}
			if(this.token){
				this.getIsWinAward();
			}
		},
		toDetail(id){
			uni.navigateTo({
				url: `../detail/index?id=${id}`
			})
		},
		newPersonRedbags(id){
			if(this.getLoginStatus == false){
				this.userLogin();
			}else{
				this.toDetail(id);
			}
		},
		// 倒计时
		newPersonCountTime () {
		  // 获取当前时间
		  let date = new Date()
		  let now = date.getTime()
		  // 设置截止时间
		  // let endDate = new Date(this.curStartTime) // this.curStartTime需要倒计时的日期
		  // let end = endDate.getTime()
		  let end = this.curStartTime
		  // 时间差
		  let leftTime = end - now
		  // 定义变量 d,h,m,s保存倒计时的时间
		  if (leftTime >= 0) {
		    // 天
		    this.day = Math.floor(leftTime / 1000 / 60 / 60 / 24)
		    // 时
		    let h = Math.floor(leftTime / 1000 / 60 / 60 % 24)
		    this.hour = h < 10 ? '0' + h : h
		    // 分
		    let m = Math.floor(leftTime / 1000 / 60 % 60)
		    this.min = m < 10 ? '0' + m : m
		    // 秒
		    let s = Math.floor(leftTime / 1000 % 60)
		    this.second = s < 10 ? '0' + s : s
		  } else {
		    this.day = 0
		    this.hour = '00'
		    this.min = '00'
		    this.second = '00'
		  }
		  // 等于0的时候不调用
		  if (Number(this.hour) === 0 && Number(this.day) === 0 && Number(this.min) === 0 && Number(this.second) === 0) {
			  console.log('到点了')
			  this.newPersonGift = false;
		    return
		  } else {
		  // 递归每秒调用newPersonCountTime方法，显示动态时间效果,
		    setTimeout(this.newPersonCountTime, 1000)
		  }
		},
		getUserStatus(){
			this.$http.get('/bargain_v1/user/status',{params: {token: this.token}})
				.then(res => {
					let data = res.data.data;
					console.log(data);
					if(res.data.code == 1){
						if(data.is_new == 1){
							this.newPersonGift = true;
							this.curStartTime = data.last_time
							this.newPersonCountTime(); //新手倒计时
							this.getNewPersonList();
						}
						if(data.first_status === 0){ //用户首次进入小程序
							this.$refs.redbagsPopup.open();
						}
					}
				}).catch(err => {
					console.log(err);
				})
		},
		getNewPersonList(){
			this.$http.get('/bargain_v1/active/new-welfare',{params: {token: this.token}})
				.then(res => {
					let data = res.data.data;
					console.log(data);
					if(res.data.code == 1){
						this.newPersonList = data;
					}
				}).catch(err => {
					console.log(err);
				})
		},
		getHotList(){
			this.$http.get('/bargain_v1/active/hot-list',{params: {token: this.token}})
				.then(res => {
					let data = res.data.data;
					console.log(data);
					if(res.data.code == 1){
						this.hotList = data;
					}
				}).catch(err => {
					console.log(err);
				})
		},
		bindGetUserInfo(e){
			console.log(e);
			if(!e.detail.iv){  
				uni.showToast({  
					icon: 'none',
					title: "登录失败！"  
				});  
				return false;  
			} else{
				let avatar = e.detail.userInfo.avatarUrl;
				let username = e.detail.userInfo.nickName;
				let router = e.currentTarget.dataset.router;
				let id = e.currentTarget.dataset.id || '';
				this.login(e.detail.userInfo);
				this.userLogin(avatar,username,router,id);
				
			}
		},
		toLottery(){
			uni.navigateTo({
				url: '../lottery/index'
			})
		},
		getIsWinAward(){ //检查是否有砍价成功的
			this.$http.get('/bargain_v1/bargain/has-new',{params: {token: this.token}})
				.then(res => {
					let data = res.data.data;
					console.log(data);
					if(res.data.code == 1){
						if(res.data.data.bargainStatus == 1){
							this.bargainData = data.bargainList[0];
							this.$refs.isWinPopup.open();
						}
					}
				}).catch(err => {
					console.log(err);
				})
		},
		toAwardsPage(){  //砍价成功弹窗去领奖
			this.$http.get('/bargain_v1/bargain/read-new',{params: {token: this.token,id: this.bargainData.id}})
				.then(res => {
					let data = res.data.data;
					if(res.data.code == 1){
						uni.navigateTo({
							url: '../awards/index',
							success: ()=> {
								this.$refs.isWinPopup.close();
							}
						})
					}else{
						uni.showToast({
							title: res.data.msg
						})
					}
				}).catch(err => {
					console.log(err);
				})
		},
		userLogin(avatar,username,router,id){
			let that = this;
			uni.login({
			  provider: 'qq',
			  success: function (loginRes) {
				let code = loginRes.code;
				that.$http.post('/bargain_v1/user/login',{
						code: code,
						avatar: avatar,
						username: username
					},{
						header: {
							'content-type': 'application/x-www-form-urlencoded'
						}
					})
					.then(res => {
						console.log(res.data);
						let data = res.data.data;
						that.setToken(data.token);
						uni.setStorageSync('uid',data.uid);
						that.getHotList();
						that.getUserStatus();
						uni.navigateTo({
							url: `../${router}/index?id=${id}`
						})
						
					}).catch(err => {
						console.log(err);
					})
			  }
			});
		},
		outTimeEnd(){
			this.unitData();
			this.getHotList();
			console.log('结束啦')
		}
		
	}
}
